.signin-screen-overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 9; }

.signin-screen {
  position: relative;
  height: 70%;
  width: 50%;
  margin: 6% 25%;
  background: url(6fff671148f2078eb9084328a2885aa6.jpg) no-repeat;
  background-image: url(6fff671148f2078eb9084328a2885aa6.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 10; }

#close-signin {
  position: absolute; }

.cta {
  position: relative; }

.btn-container {
  position: relative;
  right: 0; }

.btn-container button {
  background: transparent;
  border: none;
  color: green;
  font-family: Arial;
  font-size: 20px;
  text-decoration: underline;
  position: absolute;
  right: 0px;
  cursor: pointer; }

.btn-container button:hover {
  font-size: 25px; }

.username {
  /* display: grid; */
  position: absolute;
  top: 40%;
  left: 40%; }

.password {
  /* display: grid; */
  position: absolute;
  top: 50%;
  left: 40%; }

.login {
  position: absolute;
  top: 65%;
  left: 45%; }

.login button {
  position: relative;
  background: transparent;
  border: none;
  color: green;
  font-family: Arial;
  font-size: 20px;
  text-decoration: underline;
  cursor: pointer; }

.login button:hover {
  font-size: 20px;
  color: #a2e9a2; }

.create-account {
  position: absolute;
  top: 77%;
  left: 38%; }

.create-account button {
  position: relative;
  background: transparent;
  border: none;
  color: green;
  font-family: Arial;
  font-size: 20px;
  text-decoration: underline;
  cursor: pointer; }

.create-account button:hover {
  font-size: 20px;
  color: #a2e9a2; }
